<template>
  <div class="settings-container">
    <div class="settings-header">
      <div class="header-left">
        <el-button icon="ArrowLeft" @click="goBack"></el-button>
        <h1></h1>
      </div>
    </div>
    
    <div class="settings-content">
      <el-card class="settings-card">
        <template #header>
          <div class="card-header">
            <span></span>
          </div>
        </template>
        
        <el-form :model="userForm" label-width="100px" class="settings-form">
          <el-form-item label="ID">
            <el-input v-model="userForm.id" disabled />
          </el-form-item>
          
          <el-form-item label="">
            <el-input v-model="userForm.username" disabled />
          </el-form-item>
          
          <el-form-item label="">
            <el-input v-model="formattedCreatedAt" disabled />
          </el-form-item>
        </el-form>
      </el-card>
      
      <el-card class="settings-card">
        <template #header>
          <div class="card-header">
            <span></span>
          </div>
        </template>
        
        <el-form
          ref="usernameFormRef"
          :model="usernameForm"
          :rules="usernameRules"
          label-width="100px"
          class="settings-form"
        >
          <el-form-item label="" prop="newUsername">
            <el-input
              v-model="usernameForm.newUsername"
              placeholder="3-50"
              clearable
            />
          </el-form-item>
          
          <el-form-item>
            <el-button
              type="primary"
              @click="handleUpdateUsername"
              :loading="usernameLoading"
            >
              
            </el-button>
          </el-form-item>
        </el-form>
      </el-card>
      
      <el-card class="settings-card">
        <template #header>
          <div class="card-header">
            <span></span>
          </div>
        </template>
        
        <div class="invite-section">
          <el-alert
            title=""
            type="info"
            :closable="false"
            style="margin-bottom: 20px;"
          >
            <p></p>
            <p></p>
          </el-alert>
          
          <el-button
            type="primary"
            icon="Plus"
            @click="handleGenerateInviteCode"
            :loading="inviteLoading"
            style="margin-bottom: 20px;"
          >
            
          </el-button>
          
          <el-table :data="inviteCodes" border style="width: 100%">
            <el-table-column prop="code" label="" min-width="200">
              <template #default="{ row }">
                <el-text copyable>{{ row.code }}</el-text>
              </template>
            </el-table-column>
            <el-table-column prop="created_at" label="" width="180">
              <template #default="{ row }">
                {{ formatTime(row.created_at) }}
              </template>
            </el-table-column>
            <el-table-column prop="is_used" label="" width="100" align="center">
              <template #default="{ row }">
                <el-tag :type="row.is_used ? 'info' : 'success'">
                  {{ row.is_used ? '' : '' }}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column label="" width="120" align="center">
              <template #default="{ row }">
                <el-button
                  size="small"
                  @click="copyInviteLink(row.code)"
                  :disabled="row.is_used"
                >
                  
                </el-button>
              </template>
            </el-table-column>
          </el-table>
          
          <div style="margin-top: 20px;">
            <h4> ({{ collaborators.length }})</h4>
            <el-table :data="collaborators" border style="width: 100%; margin-top: 10px;">
              <el-table-column prop="username" label="" />
              <el-table-column prop="created_at" label="" width="180">
                <template #default="{ row }">
                  {{ formatTime(row.created_at) }}
                </template>
              </el-table-column>
              <el-table-column label="" width="120" align="center">
                <template #default="{ row }">
                  <el-button
                    type="danger"
                    size="small"
                    @click="handleDeleteCollaborator(row.id)"
                    :loading="deletingCollaboratorId === row.id"
                  >
                    
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
      </el-card>
      
      <el-card class="settings-card">
        <template #header>
          <div class="card-header">
            <span></span>
          </div>
        </template>
        
        <el-form
          ref="passwordFormRef"
          :model="passwordForm"
          :rules="passwordRules"
          label-width="100px"
          class="settings-form"
        >
          <el-form-item label="" prop="oldPassword">
            <el-input
              v-model="passwordForm.oldPassword"
              type="password"
              placeholder=""
              show-password
              clearable
            />
          </el-form-item>
          
          <el-form-item label="" prop="newPassword">
            <el-input
              v-model="passwordForm.newPassword"
              type="password"
              placeholder="6"
              show-password
              clearable
            />
          </el-form-item>
          
          <el-form-item label="" prop="confirmPassword">
            <el-input
              v-model="passwordForm.confirmPassword"
              type="password"
              placeholder=""
              show-password
              clearable
            />
          </el-form-item>
          
          <el-form-item>
            <el-button
              type="primary"
              @click="handleUpdatePassword"
              :loading="passwordLoading"
            >
              
            </el-button>
            <el-button @click="resetPasswordForm"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import { ref, reactive, computed, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import request from '../utils/request'

export default {
  name: 'UserSettings',
  setup() {
    const router = useRouter()
    const usernameFormRef = ref(null)
    const passwordFormRef = ref(null)
    const usernameLoading = ref(false)
    const passwordLoading = ref(false)
    const inviteLoading = ref(false)
    const inviteCodes = ref([])
    const collaborators = ref([])
    const deletingCollaboratorId = ref(null)
    
    const currentUser = computed(() => {
      const user = localStorage.getItem('user')
      return user ? JSON.parse(user) : {}
    })
    
    const userForm = reactive({
      id: '',
      username: '',
      created_at: ''
    })
    
    const usernameForm = reactive({
      newUsername: ''
    })
    
    const passwordForm = reactive({
      oldPassword: '',
      newPassword: '',
      confirmPassword: ''
    })
    
    const usernameRules = {
      newUsername: [
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 50, message: '3-50', trigger: 'blur' }
      ]
    }
    
    const passwordRules = {
      oldPassword: [
        { required: true, message: '', trigger: 'blur' }
      ],
      newPassword: [
        { required: true, message: '', trigger: 'blur' },
        { min: 6, message: '6', trigger: 'blur' }
      ],
      confirmPassword: [
        { required: true, message: '', trigger: 'blur' },
        {
          validator: (rule, value, callback) => {
            if (value !== passwordForm.newPassword) {
              callback(new Error(''))
            } else {
              callback()
            }
          },
          trigger: 'blur'
        }
      ]
    }
    
    const formattedCreatedAt = computed(() => {
      if (!userForm.created_at) return ''
      const date = new Date(userForm.created_at)
      return date.toLocaleString('zh-CN')
    })
    
    const loadUserInfo = async () => {
      try {
        const response = await request.get(`/users/${currentUser.value.id}`)
        if (response.success) {
          Object.assign(userForm, response.user)
        }
      } catch (error) {
        console.error('Load user info error:', error)
        ElMessage.error('')
      }
    }
    
    const handleUpdateUsername = async () => {
      try {
        await usernameFormRef.value.validate()
        
        if (usernameForm.newUsername === userForm.username) {
          ElMessage.warning('')
          return
        }
        
        usernameLoading.value = true
        
        const response = await request.put(`/users/${currentUser.value.id}/username`, {
          username: usernameForm.newUsername
        })
        
        ElMessage.success(response.message)
        
        // 
        const updatedUser = {
          ...currentUser.value,
          username: usernameForm.newUsername
        }
        localStorage.setItem('user', JSON.stringify(updatedUser))
        
        // 
        await loadUserInfo()
        usernameForm.newUsername = ''
        
      } catch (error) {
        console.error('Update username error:', error)
      } finally {
        usernameLoading.value = false
      }
    }
    
    const handleUpdatePassword = async () => {
      try {
        await passwordFormRef.value.validate()
        
        passwordLoading.value = true
        
        const response = await request.put(`/users/${currentUser.value.id}/password`, {
          old_password: passwordForm.oldPassword,
          new_password: passwordForm.newPassword
        })
        
        ElMessage.success(response.message + '')
        
        // 
        setTimeout(() => {
          localStorage.removeItem('user')
          router.push('/login')
        }, 1500)
        
      } catch (error) {
        console.error('Update password error:', error)
      } finally {
        passwordLoading.value = false
      }
    }
    
    const resetPasswordForm = () => {
      passwordForm.oldPassword = ''
      passwordForm.newPassword = ''
      passwordForm.confirmPassword = ''
      passwordFormRef.value?.clearValidate()
    }
    
    const loadInviteCodes = async () => {
      try {
        const response = await request.get(`/users/${currentUser.value.id}/invite-codes`)
        if (response.success) {
          inviteCodes.value = response.invite_codes
        }
      } catch (error) {
        console.error('Load invite codes error:', error)
      }
    }
    
    const loadCollaborators = async () => {
      try {
        const response = await request.get(`/users/${currentUser.value.id}/collaborators`)
        if (response.success) {
          collaborators.value = response.collaborators
        }
      } catch (error) {
        console.error('Load collaborators error:', error)
      }
    }
    
    const handleGenerateInviteCode = async () => {
      try {
        inviteLoading.value = true
        const response = await request.post(`/users/${currentUser.value.id}/invite-code`)
        ElMessage.success(response.message)
        await loadInviteCodes()
      } catch (error) {
        console.error('Generate invite code error:', error)
      } finally {
        inviteLoading.value = false
      }
    }
    
    const copyInviteLink = (code) => {
      const link = `${window.location.origin}/login?invite_code=${code}`
      navigator.clipboard.writeText(link).then(() => {
        ElMessage.success('')
      }).catch(() => {
        ElMessage.error('')
      })
    }
    
    const formatTime = (time) => {
      if (!time) return ''
      const date = new Date(time)
      return date.toLocaleString('zh-CN')
    }
    
    const handleDeleteCollaborator = async (collaboratorId) => {
      try {
        await ElMessageBox.confirm(
          '',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        )
        
        deletingCollaboratorId.value = collaboratorId
        
        const response = await request.delete(
          `/users/${currentUser.value.id}/collaborators/${collaboratorId}`
        )
        
        ElMessage.success(response.message)
        await loadCollaborators()
        
      } catch (error) {
        if (error !== 'cancel') {
          console.error('Delete collaborator error:', error)
        }
      } finally {
        deletingCollaboratorId.value = null
      }
    }
    
    const goBack = () => {
      router.push('/dashboard')
    }
    
    onMounted(() => {
      loadUserInfo()
      loadInviteCodes()
      loadCollaborators()
    })
    
    return {
      usernameFormRef,
      passwordFormRef,
      usernameLoading,
      passwordLoading,
      inviteLoading,
      inviteCodes,
      collaborators,
      deletingCollaboratorId,
      userForm,
      usernameForm,
      passwordForm,
      usernameRules,
      passwordRules,
      formattedCreatedAt,
      handleUpdateUsername,
      handleUpdatePassword,
      handleGenerateInviteCode,
      handleDeleteCollaborator,
      copyInviteLink,
      formatTime,
      resetPasswordForm,
      goBack
    }
  }
}
</script>

