<template>
  <div class="login-container">
    <div class="login-box">
      <div class="login-header">
        <img :src="logoImg" class="logo-img" alt="Logo" />
        <h1></h1>
        <p>RED team Asset Collaboration Platform</p>
      </div>
      
      <el-tabs v-model="activeTab" class="login-tabs" stretch>
        <el-tab-pane label="" name="login">
          <el-form
            ref="loginFormRef"
            :model="loginForm"
            :rules="loginRules"
            class="login-form"
          >
            <el-form-item prop="username">
              <el-input
                v-model="loginForm.username"
                placeholder=""
                prefix-icon="User"
                size="large"
                class="minimal-input"
              />
            </el-form-item>
            
            <el-form-item prop="password">
              <el-input
                v-model="loginForm.password"
                type="password"
                placeholder=""
                prefix-icon="Lock"
                size="large"
                show-password
                class="minimal-input"
                @keyup.enter="handleLogin"
              />
            </el-form-item>
            
            <el-form-item>
              <el-button
                type="primary"
                :loading="loginLoading"
                @click="handleLogin"
                class="login-btn"
                size="large"
                round
              >
                 
              </el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        
        <el-tab-pane label="" name="register">
          <el-alert
            v-if="!allowRegistration"
            title=""
            type="warning"
            :closable="false"
            show-icon
            style="margin-bottom: 20px;"
          >
            <template #default>
              
            </template>
          </el-alert>
          
          <el-form
            ref="registerFormRef"
            :model="registerForm"
            :rules="registerRules"
            class="login-form"
          >
            <el-form-item prop="username">
              <el-input
                v-model="registerForm.username"
                placeholder=" (3-50)"
                prefix-icon="User"
                size="large"
                class="minimal-input"
              />
            </el-form-item>
            
            <el-form-item prop="password">
              <el-input
                v-model="registerForm.password"
                type="password"
                placeholder=" (6)"
                prefix-icon="Lock"
                size="large"
                show-password
                class="minimal-input"
              />
            </el-form-item>
            
            <el-form-item prop="confirmPassword">
              <el-input
                v-model="registerForm.confirmPassword"
                type="password"
                placeholder=""
                prefix-icon="Lock"
                size="large"
                show-password
                class="minimal-input"
              />
            </el-form-item>
            
            <el-form-item prop="inviteCode">
              <el-input
                v-model="registerForm.inviteCode"
                placeholder=""
                prefix-icon="Ticket"
                size="large"
                class="minimal-input"
                @keyup.enter="handleRegister"
              />
            </el-form-item>
            
            <el-alert
              v-if="registerForm.inviteCode"
              title=""
              type="success"
              :closable="false"
              show-icon
              class="minimal-alert"
            />
            
            <el-form-item>
              <el-button
                type="success"
                :loading="registerLoading"
                @click="handleRegister"
                class="login-btn"
                size="large"
                round
              >
                 
              </el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
      </el-tabs>
      
      <div class="login-footer">
        <span @click="goToInstall"></span>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import request from '../utils/request'
//  logo.jpg  src/assets/ 
// 
import logoImg from '../assets/logo.png'

export default {
  name: 'Login',
  setup() {
    const router = useRouter()
    const activeTab = ref('login')
    const loginFormRef = ref(null)
    const registerFormRef = ref(null)
    const loginLoading = ref(false)
    const registerLoading = ref(false)
    
    const loginForm = reactive({
      username: '',
      password: ''
    })
    
    const registerForm = reactive({
      username: '',
      password: '',
      confirmPassword: '',
      inviteCode: ''
    })
    
    const validateConfirmPassword = (rule, value, callback) => {
      if (value !== registerForm.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    
    const loginRules = {
      username: [{ required: true, message: '', trigger: 'blur' }],
      password: [{ required: true, message: '', trigger: 'blur' }]
    }
    
    const registerRules = {
      username: [
        { required: true, message: '', trigger: 'blur' },
        { min: 3, max: 50, message: '3-50', trigger: 'blur' }
      ],
      password: [
        { required: true, message: '', trigger: 'blur' },
        { min: 6, message: '6', trigger: 'blur' }
      ],
      confirmPassword: [
        { required: true, message: '', trigger: 'blur' },
        { validator: validateConfirmPassword, trigger: 'blur' }
      ]
    }
    
    const allowRegistration = ref(true)
    
    const checkInstall = async () => {
      try {
        const response = await request.get('/check-install')
        if (!response.installed) {
          ElMessage.warning('')
          router.push('/install')
        } else {
          // 
          const settingResponse = await request.get('/settings/registration')
          if (settingResponse.success) {
            allowRegistration.value = settingResponse.allow_registration
          }
        }
      } catch (error) {
        console.error('Check install error:', error)
      }
    }
    
    const handleLogin = async () => {
      try {
        await loginFormRef.value.validate()
        loginLoading.value = true
        
        const response = await request.post('/login', loginForm)
        
        localStorage.setItem('user', JSON.stringify(response.user))
        ElMessage.success(response.message)
        
        router.push('/dashboard')
      } catch (error) {
        console.error('Login error:', error)
      } finally {
        loginLoading.value = false
      }
    }
    
    const handleRegister = async () => {
      try {
        await registerFormRef.value.validate()
        registerLoading.value = true
        
        const payload = {
          username: registerForm.username,
          password: registerForm.password
        }
        
        if (registerForm.inviteCode) {
          payload.invite_code = registerForm.inviteCode
        }
        
        const response = await request.post('/register', payload)
        
        ElMessage.success(response.message)
        
        loginForm.username = registerForm.username
        loginForm.password = registerForm.password
        activeTab.value = 'login'
        
        registerForm.username = ''
        registerForm.password = ''
        registerForm.confirmPassword = ''
        registerForm.inviteCode = ''
      } catch (error) {
        console.error('Register error:', error)
      } finally {
        registerLoading.value = false
      }
    }
    
    const goToInstall = () => {
      router.push('/install')
    }
    
    onMounted(() => {
      checkInstall()
      
      const urlParams = new URLSearchParams(window.location.search)
      const inviteCode = urlParams.get('invite_code')
      if (inviteCode) {
        registerForm.inviteCode = inviteCode
        activeTab.value = 'register'
        ElMessage.info('')
      }
    })
    
    return {
      activeTab,
      loginFormRef,
      registerFormRef,
      loginForm,
      registerForm,
      loginRules,
      registerRules,
      loginLoading,
      registerLoading,
      allowRegistration,
      handleLogin,
      handleRegister,
      goToInstall,
      logoImg // 
    }
  }
}
</script>

