<template>
  <div class="admin-panel">
    <div class="admin-header">
      <h1></h1>
      <el-button type="primary" @click="goBack"></el-button>
    </div>

    <el-tabs v-model="activeTab" class="admin-tabs">
      <el-tab-pane label="" name="stats">
        <el-row :gutter="20" class="stats-row">
          <el-col :span="6">
            <el-card class="stat-card">
              <div class="stat-icon users-icon">
                <el-icon><User /></el-icon>
              </div>
              <div class="stat-content">
                <div class="stat-value">{{ stats.total_users }}</div>
                <div class="stat-label"></div>
                <div class="stat-trend">7: +{{ stats.recent_users }}</div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card class="stat-card">
              <div class="stat-icon projects-icon">
                <el-icon><Folder /></el-icon>
              </div>
              <div class="stat-content">
                <div class="stat-value">{{ stats.total_projects }}</div>
                <div class="stat-label"></div>
                <div class="stat-trend">7: +{{ stats.recent_projects }}</div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card class="stat-card">
              <div class="stat-icon assets-icon">
                <el-icon><Document /></el-icon>
              </div>
              <div class="stat-content">
                <div class="stat-value">{{ stats.total_assets }}</div>
                <div class="stat-label"></div>
                <div class="stat-trend">&nbsp;</div>
              </div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card class="stat-card">
              <div class="stat-icon vulns-icon">
                <el-icon><Warning /></el-icon>
              </div>
              <div class="stat-content">
                <div class="stat-value">{{ stats.total_vulnerabilities }}</div>
                <div class="stat-label"></div>
                <div class="stat-trend">&nbsp;</div>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-tab-pane>

      <el-tab-pane label="" name="users">
        <div class="table-header">
          <h3></h3>
          <div>
            <el-button type="success" @click="showCreateUserDialog">
              <el-icon><Plus /></el-icon> 
            </el-button>
            <el-button type="primary" @click="loadUsers" :loading="usersLoading">
              <el-icon><Refresh /></el-icon> 
            </el-button>
          </div>
        </div>
        
        <el-table :data="users" style="width: 100%" v-loading="usersLoading">
          <el-table-column prop="id" label="ID" width="80" />
          <el-table-column prop="username" label="" width="200" />
          <el-table-column label="" width="150">
            <template #default="scope">
              <el-tag v-if="scope.row.is_super_admin" type="danger" size="small"></el-tag>
              <el-tag v-else-if="scope.row.is_admin" type="warning" size="small"></el-tag>
              <el-tag v-else type="info" size="small"></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="project_count" label="" width="100" />
          <el-table-column prop="asset_count" label="" width="100" />
          <el-table-column prop="created_at" label="" width="180">
            <template #default="scope">
              {{ formatDate(scope.row.created_at) }}
            </template>
          </el-table-column>
          <el-table-column label="" width="400">
            <template #default="scope">
              <el-button 
                size="small" 
                type="primary"
                @click="showEditUserDialog(scope.row)"
              >
                
              </el-button>
              <el-button 
                size="small" 
                :type="scope.row.is_admin ? 'warning' : 'primary'"
                @click="toggleAdmin(scope.row)"
                :disabled="scope.row.id === currentUser.id"
              >
                {{ scope.row.is_admin ? '' : '' }}
              </el-button>
              <el-button 
                size="small" 
                :type="scope.row.is_super_admin ? 'danger' : 'success'"
                @click="toggleSuperAdmin(scope.row)"
                :disabled="scope.row.id === currentUser.id"
              >
                {{ scope.row.is_super_admin ? '' : '' }}
              </el-button>
              <el-button 
                size="small" 
                type="danger" 
                @click="deleteUser(scope.row)"
                :disabled="scope.row.id === currentUser.id"
              >
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="" name="projects">
        <div class="table-header">
          <h3></h3>
          <el-button type="primary" @click="loadProjects" :loading="projectsLoading">
            <el-icon><Refresh /></el-icon> 
          </el-button>
        </div>
        
        <el-table :data="projects" style="width: 100%" v-loading="projectsLoading">
          <el-table-column prop="id" label="ID" width="80" />
          <el-table-column prop="name" label="" width="200" />
          <el-table-column prop="description" label="" show-overflow-tooltip />
          <el-table-column prop="creator_name" label="" width="150" />
          <el-table-column prop="asset_count" label="" width="100" />
          <el-table-column prop="created_at" label="" width="180">
            <template #default="scope">
              {{ formatDate(scope.row.created_at) }}
            </template>
          </el-table-column>
          <el-table-column label="" width="150">
            <template #default="scope">
              <el-button 
                size="small" 
                type="primary" 
                @click="viewProject(scope.row.id)"
              >
                
              </el-button>
              <el-button 
                size="small" 
                type="danger" 
                @click="deleteProject(scope.row)"
              >
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>

      <el-tab-pane label="" name="settings">
        <el-card class="settings-card">
          <template #header>
            <div class="card-header">
              <span></span>
              <el-button type="primary" @click="saveSettings" :loading="settingsLoading">
                
              </el-button>
            </div>
          </template>
          
          <el-form :model="settings" label-width="150px">
            <el-form-item label="">
              <el-input v-model="settings.site_name" placeholder="" />
            </el-form-item>
            
            <el-form-item label="">
              <el-input v-model="settings.site_description" placeholder="" />
            </el-form-item>
            
            <el-form-item label="">
              <el-switch 
                v-model="settings.allow_registration" 
                active-text="" 
                inactive-text=""
              />
              <div class="form-tip">
                
              </div>
            </el-form-item>
          </el-form>
        </el-card>
      </el-tab-pane>
    </el-tabs>

    <el-dialog 
      v-model="createUserDialogVisible" 
      title="" 
      width="500px"
      @close="resetCreateUserForm"
    >
      <el-form :model="createUserForm" label-width="100px">
        <el-form-item label="" required>
          <el-input 
            v-model="createUserForm.username" 
            placeholder="3-50"
            maxlength="50"
          />
        </el-form-item>
        <el-form-item label="" required>
          <el-input 
            v-model="createUserForm.password" 
            type="password" 
            placeholder="6"
            show-password
          />
        </el-form-item>
        <el-form-item label="">
          <el-checkbox v-model="createUserForm.is_admin"></el-checkbox>
          <el-checkbox v-model="createUserForm.is_super_admin"></el-checkbox>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="createUserDialogVisible = false"></el-button>
        <el-button type="primary" @click="createUser" :loading="createUserLoading">
          
        </el-button>
      </template>
    </el-dialog>

    <el-dialog 
      v-model="editUserDialogVisible" 
      title="" 
      width="500px"
      @close="resetEditUserForm"
    >
      <el-form :model="editUserForm" label-width="100px">
        <el-form-item label="ID">
          <el-input v-model="editUserForm.id" disabled />
        </el-form-item>
        <el-form-item label="" required>
          <el-input 
            v-model="editUserForm.username" 
            placeholder="3-50"
            maxlength="50"
          />
        </el-form-item>
        <el-form-item label="">
          <el-input 
            v-model="editUserForm.password" 
            type="password" 
            placeholder=""
            show-password
          />
          <div class="form-tip"></div>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="editUserDialogVisible = false"></el-button>
        <el-button type="primary" @click="updateUser" :loading="editUserLoading">
          
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
import { User, Folder, Document, Warning, Refresh, Plus } from '@element-plus/icons-vue'
import request from '../utils/request'

export default {
  name: 'AdminPanel',
  components: {
    User,
    Folder,
    Document,
    Warning,
    Refresh,
    Plus
  },
  setup() {
    const router = useRouter()
    const activeTab = ref('stats')
    const currentUser = JSON.parse(localStorage.getItem('user') || '{}')
    
    const stats = reactive({
      total_users: 0,
      total_projects: 0,
      total_assets: 0,
      total_vulnerabilities: 0,
      recent_users: 0,
      recent_projects: 0
    })
    
    const users = ref([])
    const usersLoading = ref(false)
    
    const projects = ref([])
    const projectsLoading = ref(false)
    
    const settings = reactive({
      site_name: '',
      site_description: '',
      allow_registration: true
    })
    const settingsLoading = ref(false)
    
    // 
    const createUserDialogVisible = ref(false)
    const createUserLoading = ref(false)
    const createUserForm = reactive({
      username: '',
      password: '',
      is_admin: false,
      is_super_admin: false
    })
    
    // 
    const editUserDialogVisible = ref(false)
    const editUserLoading = ref(false)
    const editUserForm = reactive({
      id: null,
      username: '',
      password: ''
    })
    
    const formatDate = (dateStr) => {
      if (!dateStr) return ''
      const date = new Date(dateStr)
      return date.toLocaleString('zh-CN')
    }
    
    const goBack = () => {
      router.push('/dashboard')
    }
    
    // 
    const loadStats = async () => {
      try {
        const response = await request.get('/admin/stats', {
          params: { admin_id: currentUser.id }
        })
        if (response.success) {
          Object.assign(stats, response.stats)
        }
      } catch (error) {
        console.error(':', error)
      }
    }
    
    // 
    const loadUsers = async () => {
      usersLoading.value = true
      try {
        const response = await request.get('/admin/users', {
          params: { admin_id: currentUser.id }
        })
        if (response.success) {
          users.value = response.users
        }
      } catch (error) {
        ElMessage.error('')
      } finally {
        usersLoading.value = false
      }
    }
    
    // 
    const toggleAdmin = async (user) => {
      try {
        await ElMessageBox.confirm(
          `${user.is_admin ? '' : ''} ${user.username} `,
          '',
          { type: 'warning' }
        )
        
        const response = await request.put(`/admin/users/${user.id}`, {
          admin_id: currentUser.id,
          is_admin: !user.is_admin
        })
        
        if (response.success) {
          ElMessage.success(response.message)
          loadUsers()
        }
      } catch (error) {
        if (error !== 'cancel') {
          ElMessage.error('')
        }
      }
    }
    
    // 
    const toggleSuperAdmin = async (user) => {
      try {
        await ElMessageBox.confirm(
          `${user.is_super_admin ? '' : ''} ${user.username} `,
          '',
          { type: 'warning' }
        )
        
        const response = await request.put(`/admin/users/${user.id}`, {
          admin_id: currentUser.id,
          is_super_admin: !user.is_super_admin
        })
        
        if (response.success) {
          ElMessage.success(response.message)
          loadUsers()
        }
      } catch (error) {
        if (error !== 'cancel') {
          ElMessage.error('')
        }
      }
    }
    
    // 
    const showCreateUserDialog = () => {
      createUserDialogVisible.value = true
    }
    
    // 
    const resetCreateUserForm = () => {
      createUserForm.username = ''
      createUserForm.password = ''
      createUserForm.is_admin = false
      createUserForm.is_super_admin = false
    }
    
    // 
    const createUser = async () => {
      if (!createUserForm.username || !createUserForm.password) {
        ElMessage.warning('')
        return
      }
      
      if (createUserForm.username.length < 3 || createUserForm.username.length > 50) {
        ElMessage.warning('3-50')
        return
      }
      
      if (createUserForm.password.length < 6) {
        ElMessage.warning('6')
        return
      }
      
      createUserLoading.value = true
      try {
        const response = await request.post('/admin/users', {
          admin_id: currentUser.id,
          username: createUserForm.username,
          password: createUserForm.password,
          is_admin: createUserForm.is_admin,
          is_super_admin: createUserForm.is_super_admin
        })
        
        if (response.success) {
          ElMessage.success(response.message)
          createUserDialogVisible.value = false
          resetCreateUserForm()
          loadUsers()
          loadStats()
        }
      } catch (error) {
        ElMessage.error(error.message || '')
      } finally {
        createUserLoading.value = false
      }
    }
    
    // 
    const showEditUserDialog = (user) => {
      editUserForm.id = user.id
      editUserForm.username = user.username
      editUserForm.password = ''
      editUserDialogVisible.value = true
    }
    
    // 
    const resetEditUserForm = () => {
      editUserForm.id = null
      editUserForm.username = ''
      editUserForm.password = ''
    }
    
    // 
    const updateUser = async () => {
      if (!editUserForm.username) {
        ElMessage.warning('')
        return
      }
      
      if (editUserForm.username.length < 3 || editUserForm.username.length > 50) {
        ElMessage.warning('3-50')
        return
      }
      
      if (editUserForm.password && editUserForm.password.length < 6) {
        ElMessage.warning('6')
        return
      }
      
      editUserLoading.value = true
      try {
        const updateData = {
          admin_id: currentUser.id,
          username: editUserForm.username
        }
        
        // 
        if (editUserForm.password) {
          updateData.password = editUserForm.password
        }
        
        const response = await request.put(`/admin/users/${editUserForm.id}`, updateData)
        
        if (response.success) {
          ElMessage.success(response.message)
          editUserDialogVisible.value = false
          resetEditUserForm()
          loadUsers()
        }
      } catch (error) {
        ElMessage.error(error.message || '')
      } finally {
        editUserLoading.value = false
      }
    }
    
    // 
    const deleteUser = async (user) => {
      try {
        await ElMessageBox.confirm(
          ` ${user.username} `,
          '',
          { type: 'error', confirmButtonText: '' }
        )
        
        const response = await request.delete(`/admin/users/${user.id}`, {
          params: { admin_id: currentUser.id }
        })
        
        if (response.success) {
          ElMessage.success(response.message)
          loadUsers()
          loadStats()
        }
      } catch (error) {
        if (error !== 'cancel') {
          ElMessage.error('')
        }
      }
    }
    
    // 
    const loadProjects = async () => {
      projectsLoading.value = true
      try {
        const response = await request.get('/admin/projects', {
          params: { admin_id: currentUser.id }
        })
        if (response.success) {
          projects.value = response.projects
        }
      } catch (error) {
        ElMessage.error('')
      } finally {
        projectsLoading.value = false
      }
    }
    
    // 
    const viewProject = (projectId) => {
      router.push(`/project/${projectId}`)
    }
    
    // 
    const deleteProject = async (project) => {
      try {
        await ElMessageBox.confirm(
          ` ${project.name} `,
          '',
          { type: 'error', confirmButtonText: '' }
        )
        
        const response = await request.delete(`/admin/projects/${project.id}`, {
          params: { admin_id: currentUser.id }
        })
        
        if (response.success) {
          ElMessage.success(response.message)
          loadProjects()
          loadStats()
        }
      } catch (error) {
        if (error !== 'cancel') {
          ElMessage.error('')
        }
      }
    }
    
    // 
    const loadSettings = async () => {
      try {
        const response = await request.get('/admin/settings', {
          params: { admin_id: currentUser.id }
        })
        if (response.success) {
          const settingsData = response.settings
          settings.site_name = settingsData.site_name?.value || ''
          settings.site_description = settingsData.site_description?.value || ''
          settings.allow_registration = settingsData.allow_registration?.value === 'true'
        }
      } catch (error) {
        ElMessage.error('')
      }
    }
    
    // 
    const saveSettings = async () => {
      settingsLoading.value = true
      try {
        const response = await request.put('/admin/settings', {
          admin_id: currentUser.id,
          settings: {
            site_name: settings.site_name,
            site_description: settings.site_description,
            allow_registration: settings.allow_registration ? 'true' : 'false'
          }
        })
        
        if (response.success) {
          ElMessage.success(response.message)
        }
      } catch (error) {
        ElMessage.error('')
      } finally {
        settingsLoading.value = false
      }
    }
    
    onMounted(() => {
      loadStats()
      loadUsers()
      loadProjects()
      loadSettings()
    })
    
    return {
      activeTab,
      currentUser,
      stats,
      users,
      usersLoading,
      projects,
      projectsLoading,
      settings,
      settingsLoading,
      createUserDialogVisible,
      createUserLoading,
      createUserForm,
      editUserDialogVisible,
      editUserLoading,
      editUserForm,
      formatDate,
      goBack,
      loadUsers,
      toggleAdmin,
      toggleSuperAdmin,
      showCreateUserDialog,
      resetCreateUserForm,
      createUser,
      showEditUserDialog,
      resetEditUserForm,
      updateUser,
      deleteUser,
      loadProjects,
      viewProject,
      deleteProject,
      saveSettings
    }
  }
}
</script>

