}

:deep(.tech-dialog) {
  border-radius: 12px; overflow: hidden;
  .el-dialog__header { margin: 0; padding: 20px; border-bottom: 1px solid $border-color; background: #fff; .el-dialog__title { font-size: 16px; font-weight: 600; } }
