<template>
  <div class="dashboard-container">
    <div class="dashboard-header">
      <div class="header-left">
        <div class="logo-area">
          <span class="logo-icon"></span>
          <h1></h1>
        </div>
        <span class="divider">/</span>
        <span class="welcome-text">Hi, {{ currentUser.username }}</span>
      </div>
      <div class="header-right">
        <el-button v-if="currentUser.is_super_admin" @click="goToAdmin" type="warning" link icon="Tools"></el-button>
        <el-button @click="goToBigScreen" type="primary" link icon="DataAnalysis"></el-button>
        <el-button @click="goToCredentials" type="success" link icon="Key"></el-button>
        <el-button @click="goToSettings" type="info" link icon="Setting"></el-button>
        <el-button @click="handleLogout" type="danger" link></el-button>
      </div>
    </div>
    
    <div class="dashboard-content">
      <div class="content-wrapper">
        <div class="action-bar">
          <div class="bar-left">
            <h2></h2>
            <span class="project-count" v-if="projects.length"> {{ projects.length }} </span>
          </div>
          <div class="bar-right">
            <el-button 
              :icon="Refresh" 
              circle 
              @click="loadProjects" 
              title=""
              class="tech-icon-btn"
            />
            <el-button
              type="primary"
              :icon="Plus"
              @click="showCreateDialog = true"
              class="create-btn"
            >
              
            </el-button>
          </div>
        </div>
        
        <div class="projects-grid" v-loading="loading">
          <el-empty v-if="projects.length === 0 && !loading" description="" />
          
          <div
            v-for="project in projects"
            :key="project.id"
            class="project-card"
            @click="goToProject(project.id)"
          >
            <div class="card-header">
              <div class="title-group">
                <div class="project-icon">{{ project.name.charAt(0).toUpperCase() }}</div>
                <h3>{{ project.name }}</h3>
              </div>
              <el-dropdown @command="handleCommand" trigger="click">
                <div class="more-btn" @click.stop>
                  <el-icon><MoreFilled /></el-icon>
                </div>
                <template #dropdown>
                  <el-dropdown-menu>
                    <el-dropdown-item
                      :command="{ action: 'delete', id: project.id }"
                      icon="Delete"
                      class="danger-item"
                    >
                      
                    </el-dropdown-item>
                  </el-dropdown-menu>
                </template>
              </el-dropdown>
            </div>
            
            <div class="card-body">
              <p class="description">{{ project.description || '...' }}</p>
            </div>
            
            <div class="card-footer">
              <div class="tags-group">
                <span class="mini-tag asset-tag">{{ project.asset_count }} </span>
                <span class="mini-tag user-tag">{{ project.creator_name }}</span>
              </div>
              <span class="time">{{ formatTime(project.created_at).split(' ')[0] }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    
    <el-dialog
      v-model="showCreateDialog"
      title=""
      width="480px"
      class="minimal-dialog"
    >
      <el-form
        ref="createFormRef"
        :model="createForm"
        :rules="createRules"
        label-position="top"
      >
        <el-form-item label="" prop="name">
          <el-input
            v-model="createForm.name"
            placeholder=""
            maxlength="100"
            show-word-limit
          />
        </el-form-item>
        
        <el-form-item label="" prop="description">
          <el-input
            v-model="createForm.description"
            type="textarea"
            :rows="2"
            placeholder=""
            maxlength="500"
            show-word-limit
            resize="none"
          />
        </el-form-item>
      </el-form>
      
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="showCreateDialog = false"></el-button>
          <el-button type="primary" @click="handleCreate" :loading="createLoading">
            
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage, ElMessageBox } from 'element-plus'
// 
import { MoreFilled, Refresh, Plus } from '@element-plus/icons-vue'
import request from '../utils/request'

export default {
  name: 'Dashboard',
  components: {
    MoreFilled
  },
  setup() {
    const router = useRouter()
    const loading = ref(false)
    const createLoading = ref(false)
    const showCreateDialog = ref(false)
    const createFormRef = ref(null)
    const projects = ref([])
    
    const currentUser = computed(() => {
      const user = localStorage.getItem('user')
      return user ? JSON.parse(user) : {}
    })
    
    const createForm = reactive({
      name: '',
      description: ''
    })
    
    const createRules = {
      name: [
        { required: true, message: '', trigger: 'blur' },
        { min: 2, max: 100, message: '2-100', trigger: 'blur' }
      ]
    }
    
    const loadProjects = async () => {
      loading.value = true
      try {
        const response = await request.get(`/projects?user_id=${currentUser.value.id}`)
        projects.value = response.projects
      } catch (error) {
        console.error('Load projects error:', error)
      } finally {
        loading.value = false
      }
    }
    
    const handleCreate = async () => {
      try {
        await createFormRef.value.validate()
        createLoading.value = true
        
        const response = await request.post('/projects', {
          ...createForm,
          user_id: currentUser.value.id
        })
        
        ElMessage.success(response.message)
        showCreateDialog.value = false
        createForm.name = ''
        createForm.description = ''
        
        await loadProjects()
      } catch (error) {
        console.error('Create project error:', error)
      } finally {
        createLoading.value = false
      }
    }
    
    const handleCommand = async (command) => {
      if (command.action === 'delete') {
        try {
          await ElMessageBox.confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          })
          
          await request.delete(`/projects/${command.id}`)
          ElMessage.success('')
          await loadProjects()
        } catch (error) {
          if (error !== 'cancel') {
            console.error('Delete project error:', error)
          }
        }
      }
    }
    
    const goToProject = (id) => {
      router.push(`/project/${id}`)
    }
    
    const goToSettings = () => {
      router.push('/settings')
    }
    
    const goToAdmin = () => {
      router.push('/admin')
    }
    
    const goToBigScreen = () => {
      router.push('/bigscreen')
    }
    
    const goToCredentials = () => {
      router.push('/credentials')
    }
    
    const handleLogout = () => {
      localStorage.removeItem('user')
      ElMessage.success('')
      router.push('/login')
    }
    
    const formatTime = (time) => {
      if (!time) return ''
      const date = new Date(time)
      return date.toLocaleString('zh-CN')
    }
    
    onMounted(() => {
      loadProjects()
    })
    
    return {
      loading,
      createLoading,
      showCreateDialog,
      createFormRef,
      projects,
      currentUser,
      createForm,
      createRules,
      loadProjects,
      handleCreate,
      handleCommand,
      goToProject,
      goToSettings,
      goToAdmin,
      goToBigScreen,
      goToCredentials,
      handleLogout,
      formatTime,
      //  template 
      Refresh,
      Plus
    }
  }
}
</script>

